.App {
  text-align: center;
  background-image: linear-gradient(180deg, navy, violet);
  height: 100%;
  min-height: 100vh;
}

.paragraph {
  color: white;
}

h1 {
  font-size: larger;
  color: white;
}

.homeImg {
  height: 50vmin;
}

.navBar {
  height: 80px;
  color: white;
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 1em;
  padding-left: 5px;
  padding-left: 5px;
}

.navigation.active {
  left: 0;
  transition: 350ms;
  background: black;
}

.navigation {
  display: flex;
  width: 250px;
  height: 100vh;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.navLink:hover {
  font-weight: bold;
  background-color: lightgrey;
}

.navLink {
  font-weight: bold;
  border-style: outset;
  border-color: grey;
  list-style-type: none;
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 8px 0px 8px 16px;
  color: black;
  background-color: ivory;
}
.menuBars {
  top: 0;
  color: white;
  font-size: 2rem;
  background: none;
}
.nav-menu {
  width: inherit;
  padding-right: 5px;
  justify-content: center;
  background-color: black;
}
